.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s linear;
  z-index: 1;
}
.active {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: center;
  position: absolute;
  visibility: visible;
  opacity: 1;
  transition: visibility 0.5s, opacity 0.5s linear;
  z-index: 1;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 3;
}
.container {
  max-width: 358px;
  background-color: white;
  position: relative;
  top: 50%;
  left: 50%;
  border-radius: 4%;
  transform: translate(-50%, -50%);
  padding-left: 36px;
  padding-right: 36px;
}

.btnClose {
  position: fixed;
  top: -40px;
  right: -40px;
  background-image: url("../../images/Close\ Icon.png");
  background-repeat: no-repeat;
  background-size: 40px;
  box-sizing: border-box;
  background-color: transparent;
  width: 40px;
  height: 40px;
  border: none;
}

.btnClose:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .btnClose {
    width: 26px;
    height: 26px;
    top: -40px;
    right: 0;
    background-size: 22px;
  }
}

.expression {
  display: flex;
  font-family: Inter;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  padding: 34px 0 54px 0;
  margin: 0;
  height: 23px;
  align-items: center;
  text-align: center;
}

@media screen and (max-width: 550px) {
  .expression {
    font-size: 18px;
    line-height: 22px;
  }
}

.img {
  display: flex;
  margin: 0 auto;
  width: 250px;
  height: 250px;
  padding-top: 40px;
}
